.svg {
  height: 750px;
}

.st0 {
  display: none;
}
.st1 {
  display: inline;
}
.st2 {
  fill: #eaeaea;
  cursor: pointer;
  stroke: #eaeaea;
  stroke-miterlimit: 10;
  stroke-width: 0px;
  transition: fill 0.2s;
  &:hover {
    fill: var(--seed-color-primary);
  }
}

.active {
  fill: var(--seed-color-primary);
}
